import React, { Component } from 'react';
import { graphql } from '@apollo/client/react/hoc';
import { GET_EMPLOYEES } from '../graphql/queries';

class EmployeeTable extends Component {
  render() {
    const { loading, error, getAllEmployees_db } = this.props.data;

    if (loading) return <p>Loading...</p>;
    if (error) return <p>Error: {error.message}</p>;

    return (
      <>
        <h2>{JSON.stringify(getAllEmployees_db)}</h2>
        <table border={5} width="100%">
          <tbody>
            <tr>
              <th>First Name</th>
              <th>Last Name</th>
              <th>Age</th>
              <th>Date of Joining</th>
              <th>Title</th>
              <th>Department</th>
              <th>Employee Type</th>
              <th>Current Status</th>
            </tr>

            {getAllEmployees_db.map((Employee) => (
              <tr key={Employee.FirstName}>
                <td>{Employee.FirstName}</td>
                <td>{Employee.LastName}</td>
                <td>{Employee.Age}</td>
                <td>{Employee.DateOfJoining}</td>
                <td>{Employee.Title}</td>
                <td>{Employee.Department}</td>
                <td>{Employee.EmployeeType}</td>
                <td>{Employee.CurrentStatus}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </>
    );
  }
}

export default graphql(GET_EMPLOYEES)(EmployeeTable);
